<template>
  <div class="text-left">
    <v-toolbar flat>
      <v-toolbar-title class="mr-6">
        <span>{{ toolbarTitle }}</span>
        <header-selector :grid-config-change="fetchSetGridHeaders" :fieldKeys="itemKeys" :tableName="tableName" />
        <tooltip-icon-button
          btnColor="default"
          btnIcon="mdi-sync"
          :btnTooltip="refreshToolTipTitle"
          buttonSize="medium"
          :onClick="() => refreshGrid()"
        >
        </tooltip-icon-button
      ></v-toolbar-title>
      <grid-items-counter :num="selectedItems.length"></grid-items-counter>
      <v-btn v-if="isCreatable" x-small class="mr-2 btn-height-22 white-text" color="green" @click="onCreateDialogOpen">
        <span>Create</span>
        <v-icon x-small right> mdi-plus</v-icon>
      </v-btn>
      <v-btn
        x-small
        color="error"
        @click="onDeleteSelected"
        v-if="isDeletable"
        :disabled="!atleastOneGridItemSelected"
        class="mr-2 btn-height-22 white-text"
      >
        <span>Delete</span>
        <v-icon x-small right> mdi-delete</v-icon>
      </v-btn>
      <v-text-field
        v-model="search"
        @input="onSearchChange"
        append-icon="mdi-magnify"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
    </v-toolbar>
    <v-data-table
      class="text-left elevation-1"
      :headers="headers"
      :search="search"
      :items="items"
      v-model="selectedItems"
      :custom-filter="filterCriteria"
      item-key="name"
      sort-by="name"
      group-by="network_uuid"
      show-select
      @toggle-select-all="val => onSelectAll(val)"
    >
      <template v-slot:item.name="{ item }">
        <text-button
          :text="`${item.name}`"
          :onClick="() => redirectTo(viewNames.points, item.network_uuid, item.uuid)"
        ></text-button>
      </template>
      <template v-slot:group.header="{ headers, items, toggle, group, isOpen }">
        <td :colspan="headers.length">
          <div class="network-group-wrapper">
            <v-checkbox
              v-if="networkModel[group]"
              class="mt-0"
              v-model="networkModel[group].isSelected"
              color="nubeAqua darken-3"
              @click="value => onNetworkSelection(group, value)"
              :value="networkModel[group].isSelected"
              hide-details
            ></v-checkbox>
            <v-btn @click="toggle" x-small icon>
              <v-icon>{{ isOpen ? 'mdi-chevron-double-down' : 'mdi-chevron-double-right' }}</v-icon>
            </v-btn>
            <div class="subtitle-2" v-if="networkModel[group]">
              <text-button
                :text="`Network: ${networkModel[group].name}`"
                :onClick="() => redirectTo(viewNames.devices, group)"
              ></text-button>
            </div>
          </div>
        </td>
      </template>
    </v-data-table>
  </div>
</template>
<script>
import ROUTES from '../../../constants/routes';
import TextButton from '../../Common/TextButton.vue';
import GridItemsCounter from '../../Common/gridItemsCounter.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
import { rfNetworkService } from '../../../services/rfNetworkService';
import HeaderSelector from '../../HeaderSelector/headerSelector.vue';
import * as headerBuilderService from '../../../services/headerBuilderService';
import * as gridTableHeadersService from '../../../services/gridTableHeadersService';
import * as gridNames from '../../../constants/gridNames';

const viewNames = {
  devices: 'devices',
  points: 'points',
};
const redirectMap = {
  [viewNames.devices]: networkId => ROUTES.rfDevices.replace(':networkId', networkId),
  [viewNames.points]: (networkId, deviceId) =>
    ROUTES.rfPoints.replace(':networkId', networkId).replace(':deviceId', deviceId),
};

export default {
  name: 'rf-all-network-devices-grid',
  components: { GridItemsCounter, TooltipIconButton, TextButton, HeaderSelector },
  data() {
    return {
      selectedItems: [],
      tableName: gridNames.RF_ALL_NETWORK_DEVICES_GRID,
      viewNames: viewNames,
      search: '',
      searchItems: [],
      networks: [],
      networkModel: {},
      toolbarTitle: 'All Network Devices',
      networks: [],
      headers: [],
    };
  },
  computed: {
    atleastOneGridItemSelected() {
      return this.selectedItems.length > 0;
    },
    deleteAllTitle() {
      return `Are you sure to delete ${this.selectedItems.length} item(s)?`;
    },
    refreshToolTipTitle() {
      return `Refresh ${this.toolbarTitle}`;
    },
    isCreatable() {
      return typeof this.creatable === 'undefined' ? true : this.creatable;
    },
    isDeletable() {
      return typeof this.deletable === 'undefined' ? true : this.deletable;
    },

    items() {
      return this.networks.reduce((acc, network) => {
        if (!Array.isArray(network.devices)) {
          return acc;
        }
        const formattedDevices = network.devices.map(device => ({
          ...device,
          ...Object.keys(network).reduce((acc, networkKey) => {
            const newKey = 'network_' + networkKey;
            acc[newKey] = network[networkKey];
            return acc;
          }, {}),
        }));
        acc.push(...formattedDevices);
        return acc;
      }, []);
    },
    itemKeys() {
      return Array.isArray(this.networks) && this.networks.length > 0 && Object.keys(this.networks[0]);
    },
  },
  mounted() {
    this.fetchSetGridHeaders();
    this.fetchNetworkDevices();
  },
  methods: {
    refreshGrid() {
      this.search = '';
      this.networks = [];
      this.networkModel = {};
      this.selectedItems = [];
      this.fetchNetworkDevices();
    },
    fetchNetworkDevices() {
      this.isFetchingData = true;
      rfNetworkService
        .fetch({ with_devices: true })
        .then(data => {
          this.networks = data;
          this.createNetworkModel(this.networks);
        })
        .finally(() => {
          this.isFetchingData = false;
        });
    },
    getDivider() {
      return {
        divider: true,
        isHeader: false,
      };
    },
    createNetworkModel(networks) {
      this.networkModel = networks.reduce((acc, network) => {
        acc[network.uuid] = {
          ...network,
          isSelected: false,
        };
        return acc;
      }, {});
    },
    onDeleteSelected() {},
    onCreateDialogOpen() {},
    selectNetwork() {},
    onNetworkSelection(networkUUID) {
      const isSelected = this.networkModel[networkUUID].isSelected;
      if (isSelected) {
        const devices = this.items
          .map(item => (item.network_uuid === networkUUID ? { ...item } : null))
          .filter(val => val);
        this.selectedItems = [...this.selectedItems, ...devices];
      } else {
        this.selectedItems = this.selectedItems.filter(val => val.network_uuid !== networkUUID);
      }
    },
    onSelectAll({ value }) {
      Object.keys(this.networkModel).forEach(networkUUID => {
        this.networkModel[networkUUID].isSelected = value;
      });
    },
    getTableHeaders(name, key, item, level = 0) {
      return {
        name,
        key: key,
        uuid: item.uuid,
        level: level,
        selected: false,
        isHeader: true,
      };
    },
    redirectTo(viewName, uuid, deviceUUID) {
      if (!uuid) {
        return;
      }
      let destination = redirectMap[viewName];
      if (typeof destination === 'function') {
        this.$router.push({ path: destination(uuid, deviceUUID) });
      }
    },
    fetchSetGridHeaders() {
      gridTableHeadersService
        .fetch(this.tableName)
        .then(gridConfig => {
          const gridHeaders = headerBuilderService.gridHeadersResolver(this.tableName, gridConfig);
          this.headers = headerBuilderService.buildHeaders()(gridHeaders);
        })
        .catch(err => {
          this.headers = gridNames.DEFAULT_HEADERS[this.tableName];
        });
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>
<style>
.network-group-wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
</style>
